import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { Employee } from './employee';
import { EmployeesService } from './employees.service';
import { IListItemsPage } from 'src/app/services/list-items-service';

@Injectable()
export class BirthdaysResolver implements Resolve<IListItemsPage<Employee>> {
  constructor(private employeesService: EmployeesService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IListItemsPage<Employee>> {
    const month = new Date().getMonth() + 1;
    return this.employeesService.getItemsPaged({
        filter: `slBirthdayMonth eq ${month}`,
        orderBy: [['slBirthdayDay', true], ['slFullName', true]]
      });
  }
}
